.splashfooter-holder {
  background-color: #23272a;
  height: fit-content;
  display: flex;
  justify-content: center;
  position: relative;
}

.splashfooter-holder > * {
  background-color: transparent;
}

.splashfooter-text {
  padding: 80px 20px;
  max-width: 1180px;
  width: calc(100% - 40px);
  position: relative;
}

.splashfooter-linkholder {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.splashfooter-linkholder > h3 {
  width: 180px;
  margin-bottom: 20px;
  color: #5865f2;
  font-size: 16px;
  cursor: default;
  font-weight: 800;
}

.splashfooter-linkholder > ul {
  width: 180px;
  margin-bottom: 40px;
}

.splashfooter-linkholder > ul > li {
  margin-top: 20px;
}

.splashfooter-linkholder > ul > li > a {
  font-size: 14px;
  color: white;
  text-decoration: none;
  font-weight: 400;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.splashfooter-linkholder > ul > li > a div {
  margin-left: 10px;
}

.splashfooter-linkholder > ul > li > a:hover div {
  text-decoration: underline;
}

.footer-hr {
  background-color: #5865f2;
  outline: none;
  height: 1px;
  border: none;
}

.footer-bottom {
  height: 71px;
  max-width: 1180px;
  width: 100%;
  display: flex;

  justify-content: space-between;
  align-items: flex-end;
}

.footer-signup {
  text-decoration: none;
  color: white;
  background-color: #5865f2;
  border-radius: 40px;
  padding: 13px 16px;
  font-size: 13px;
  font-weight: 400;
  margin-left: 111px;
  transition: background-color 0.2s, box-shadow 0.2s;
}

.footer-signup:hover {
  background-color: #8690f5;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 8px 15px;
  transition: background-color 0.2s, box-shadow 0.2s;
}
