.navbar {
  font-family: "gg2 sans", "Noto Sans", "Helvetica Neue", Helvetica, Arial,
    sans-serif;

  position: absolute;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  z-index: 10;
}

.navbar-holder {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 12px 40px;
  font-size: 15px;
  width: 100%;
  max-width: 1180px;
}

.navbar-links {
  /* margin: 10px; */
  /* padding: 10px; */
  display: flex;
}

.navbar-links > a {
  margin: 10px;
  padding: 10px;

  text-decoration: none;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar-links > a div {
  margin-left: 10px;
}

.navbar-links > a:hover div {
  text-decoration: underline;
}

.navbar-login {
  text-decoration: none;
  color: black;
  background-color: white;
  border-radius: 40px;
  padding: 13px 16px;
  font-size: 13px;
  font-weight: 400;
  margin-left: 111px;
  transition: color 0.2s, box-shadow 0.2s;
  font-size: 15px;
  font-family: "gg2 sans";
  font-weight: 400;
}

.navbar-login:hover {
  color: #404eed;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 8px 15px;
  transition: color 0.2s, box-shadow 0.2s;
}

.navbar-links > a:hover,
.navbar-login:hover,
.footer-signup:hover {
  transform: scale(1.05);
  transition: transform 0.2s;
}
